import { get as getValue } from 'lodash-es';
import qs from 'query-string';

import store from '../store/store';
import { setWechatJsReady, setWechatSessionId } from '../store/actions';
import config from '../utils/config';
import WechatUnificationService from '../service/WechatUnificationService';
import confirm from '../components/Confirm/Confirm';

const { dispatch } = store;

// session hibernate and wx-js-sdk initial
const wechatCode = qs.parse(window.location.search).code;

function startConfig(appId, timestamp, nonceStr, signature, cb) {
	const wx = window.wx;
	wx.config({
		debug: true, // ,apialertpclogpc
		appId, // 
		timestamp, // 
		nonceStr, // 
		signature,// 
		jsApiList: ['closeWindow','checkJsApi'] // JS
	});
	wx.ready(function(){
        console.log('valild')
		cb && cb(); // ready hook
	});
	wx.error(function(res){
		console.error(res);
	});
}

export function initWechatSDK(cb) {
    console.log(config.channelId)
    WechatUnificationService.GetSessionId(config.channelId, wechatCode)
    .then((data) => {
        console.log(data.toObject())
        
        const sessionId = getValue(data.toObject(), 'sessionid');
        confirm({ content: sessionId });
        if (sessionId) {
            return sessionId;
        }
        return Promise.reject(new Error('session '));
    })
    // Promise.resolve('wx9a33d01397cc5e88')
    .then((sessionId) => {
        dispatch(setWechatSessionId(sessionId)); // temp sessinId globaly
        const { origin, pathname, search } = window.location;
        const url = `${origin}${pathname}${search}`;
        // const url = 'http%3a%2f%2fwww.codinglobster.cn%2fembedded-web%2faccount%2findex.html';
        console.log(url)
        return WechatUnificationService.GetJsApiSignature(sessionId, url);
    }).then((data) => {
        console.log(data.toObject())
        const { timestamp, noncestr, signature } = data.toObject();

            startConfig(config.channelId, timestamp, noncestr, signature, () => {
                console.log('vaild')
                dispatch(setWechatJsReady(true)); // able to use wx-js-sdk
                cb && cb();
        });
    });
}